.row_wrapper {
  color: white;
  background-color: transparent;
  margin-bottom: 10px;
  z-index: 3 !important;
  margin-left: 5vw;
}

.row_title {
  font-size: 1.4vw;
  letter-spacing: 0.7px;
  margin-bottom: 10px;
  font-weight: bold;
  z-index: 1 ;
}

.row_card {
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: center;
  height: 130px;
  color: white;
}

.row_card::-webkit-scrollbar {
  display: none;
}
